.books-style {
    height: 100vh;
}

.card {
    display: grid;
    grid-template-columns: 1fr;
    gap: 40px;
    padding: 20px;
    
}

.card img{
    width: 200px;
    height: 200px;
}

.card article {
    box-shadow: 0 10px 10px rgba(0, 0, 255 0.5);
}
